<template>
	<view>
		<view class="p-3 result-top-wrap">
			<view class="result-wrap">
				<text @click="back" class="fanhui_icon iconfont icon-fanhui1"></text>
				<text class="result">考试成绩</text>
			</view>
		</view>
		
		<!-- 下方区域-->
		<view class="bottom-wrap">
			<!-- 圆形白底 -->
			<view class="circle-wrap">
				<view class="circle-green">
					<view class="score-title">本次得分</view>
					<view v-if="resultData.isview == '0'" class="score-val">待批阅</view>
					<view v-else-if="resultData.isview == '1' && resultData.showrs == '1'"  class="score-val">{{resultData.score}}</view>
					<view v-else-if="resultData.showrs == '0'"  class="score-val">已隐藏</view>
				</view>
			</view>
			
			<image class="end_kaoshi" src="../../static/images/end_kaoshi.png" mode="aspectFit"></image>
			
			<!-- 统计区域 -->
			<view class="kaoshi-tongji">
				<view class="kaoshi-tj-item">
					<text class="kaoshi-tj-one">{{resultData.length}}</text>
					<text class="kaoshi-tj-two">答题用时(分)</text>
				</view>
				<view class="kaoshi-tj-item">
					<text class="kaoshi-tj-one">{{resultData.correct_nums}}</text>
					<text class="kaoshi-tj-two">正确(道)</text>
				</view>
				<view class="kaoshi-tj-item">
					<text class="kaoshi-tj-one">{{resultData.error_nums}}</text>
					<text class="kaoshi-tj-two">错误(道)</text>
				</view>
			</view>
			
			<!-- 查看按钮区域 -->
			<view class="view-btn-wrap">
				
				<!-- 未批阅显示灰色按钮	 -->
				<block v-if="resultData.isview == '0'">
					<text class="view-btn-item ban-click">查看全部解析</text>
					<text class="view-btn-item ban-click">只看错题解析</text>
				</block>
				
				<block v-else>
					<!-- 题目显示 不显示答案时 才显示题目 -->
					<block v-if="resultData.showq == '1' && resultData.showans == '0'">
						<text class="ans-hide-txt">本次考试已设置答案隐藏</text>
						<text @click="viewQueHistory" class="view-btn-item">查看详情</text>
					</block>
					<!-- 题目隐藏图片 -->
					<view v-if="resultData.showq == '0' && resultData.showans != '0'" class="que-hide-wrap">
						<image class="xiangzi" src="../../static/images/xiangzi.png" mode="aspectFill"></image>
						<text class="que-hide-txt">本次考试已设置题目隐藏</text>
					</view>
					
					<!-- 批阅后答案显示 -->
					<block v-if="resultData.isview == '1' && resultData.showans != '0'" class="view-btn-item">
						<text @click="viewAllJiexi" class="view-btn-item">查看全部解析</text>
						<text @click="viewCuoJiexi" class="view-btn-item">只看错题解析</text>
					</block>	
					
					<text v-if="resultData.canre == '1'" @click="openKaoshiInfo" class="view-btn-item">再考一次</text>
					
				</block>
					
			</view>
			
			
			
		</view>
		
	</view>
</template>

<script>
	const app = getApp();
	
	export default {
		components:{
		},
		data() {
			return {
				testsid: '', 
				resultData: {},
				userinfo: ''
			}
		},
		onLoad(option) {
			
			let userInfoBySto = uni.getStorageSync('userinfo');
			if(!userInfoBySto) {
				uni.navigateTo({
					url: '../login/login',
				});
			}
			this.userinfo = userInfoBySto;
			this.testsid = option.testsid;
			this.getKaoshiResult(this.testsid);
		},
		methods: {
			
			// 查看试题详情 
			viewQueHistory() {
				uni.navigateTo({
					url: '../kaoshi_history/kaoshi_history?testsid=' + this.testsid,
				});
			},
			// 重新考试
			openKaoshiInfo() {
				uni.navigateTo({
					url: '../kaoshi_info/kaoshi_info?testsid=' + this.testsid + '&type=' + '0',
				});
			},
			// 查看错题解析
			viewCuoJiexi() {
				uni.navigateTo({
					url: '../kaoshi_jiexi/kaoshi_jiexi?testsid=' + this.testsid + '&type=' + '2',
				});
			},
			// 查看全部解析
			viewAllJiexi() {
				uni.navigateTo({
					url: '../kaoshi_jiexi/kaoshi_jiexi?testsid=' + this.testsid + '&type=' + '3',
				});
			},
			// 获取成绩
			getKaoshiResult(testsid) {
				let gData = app.globalData;
				
				uni.request({
					url: gData.site_url + 'Tests.GetScore',
					method: 'GET',
					data: {
						// uid token testsid
						'uid' : this.userinfo.id,
						'token' : this.userinfo.token,
						'testsid' : testsid
					},
					success: res => {
						console.log(res);
						if(res.data.data.code != '0') {
							uni.showToast({
								icon: 'none',
								title: res.data.data.msg
							});
							return;
						}
						this.resultData = res.data.data.info[0];
						
					},
					fail: () => {
						uni.showToast({
							icon: 'none',
							title: '网络错误'
						});
					},
				});
			},
			back() {
				// 交卷了则返回到考试detail页
				uni.navigateTo({
					url: '../kaoshi_detail/kaoshi_detail?tid=' + this.testsid,
				});
			}
		}
	}
</script>

<style>
	
	page {
		background-color: #FAFAFA !important;
	}
	
	.result-top-wrap { 
		margin: auto; 
		overflow: hidden; 
	}
	.result-wrap { 
		position: relative;
		width: 100%; 
		height: 350rpx; 
		text-align: center; 
		line-height: 100rpx; 
		z-index: 99;
	}
	.result-wrap:after { 
		width: 140%;
	    height: 100%;
	    position: absolute;
	    left: -20%;
		top: -30rpx; 
		z-index: -1; 
		content: ''; 
		border-radius: 0 0 50% 50%; 
		background: linear-gradient(#2ED3AA, #0BE8A7); 
	}
	
	.fanhui_icon {
		position: absolute;
		left: 0;
		color: #FFFFFF;
	}
	
	.result {
		font-size: 32rpx;
		color: #FFFFFF;
	}
	
	/* 下方数据区域 */
	.bottom-wrap {
		height: 900rpx;
		width: 90%;
		background-color: grey;
		margin: 0 auto;
		position: relative;
		top: -140rpx;
		border-radius: 20rpx;
		background-color: #FFFFFF;
		z-index: 999;
		box-shadow:rgba(0,0,0,0.1) 0px 0px 5px;
	}
		
	/* 圆白底*/
	.circle-wrap {
		width: 280rpx;
		height: 280rpx;
		background-color: #FFFFFF;
		border-radius: 50%;
		position: absolute;
		top: -120rpx;
		left: 50%;
		-webkit-transform: translateX(-50%);
		transform: translateX(-50%);
		display: flex;
		align-items: center;
		justify-content: center;
	}
	
	.circle-green {
		width: 240rpx;
		height: 240rpx;
		border-radius: 50%;
		background-color: #38DAA6;
	}
	
	.score-title {
		text-align: center;
		color: #FFFFFF;
		margin-top: 60rpx;
		font-size: 20rpx;
	}
	
	.score-val {
		text-align: center;
		font-size: 40rpx;
		color: #FFF600;
	}
	
	/* 完成考试图片 */
	.end_kaoshi {
		width: 400rpx;
		position: absolute;
		top: -100rpx;
		left: 50%;
		-webkit-transform: translateX(-50%);
		transform: translateX(-50%);
	}
	
	/* 统计区域 */
	.kaoshi-tongji {
		width: 600rpx;
		height: 200rpx;
		background-color: #FAFAFA;
		position: absolute;
		top: 240rpx;
		left: 50%;
		-webkit-transform: translateX(-50%);
		transform: translateX(-50%);
		display: flex;
	}
	
	
	.kaoshi-tj-item {
		height: 100%;
		width: 33%;
		text-align: center;
		display: flex;
		flex-direction: column;
	}
	
	.kaoshi-tj-one {
		font-size: 28rpx;
		font-weight: bold;
		margin-top: 60rpx;
	}
	
	.kaoshi-tj-two {
		font-size: 20rpx;
		color: #969696;
	}
	
	/* 查看区域 */
	.view-btn-wrap {
		height: 440rpx;
		padding-top: 40rpx;
		position: absolute;
		top: 426rpx;
		left: 0;
		width: 100%;
		display: flex;
		flex-direction: column;
		align-items: center;
	}
	
	.view-btn-item {
		width: 600rpx;
		text-align: center;
		height: 80rpx;
		line-height: 90rpx;
		margin-bottom: 24rpx;
		border-radius: 40rpx;
		color: #38DAA6;
		border: 2rpx solid #38DAA6;
	}
	
	/* 禁止点击 */
	.ban-click {
		color: #C7C7C7;
		border: 2rpx solid #DCDCDC;
	}
	
	.xiangzi {
		display: inline-block;
		width: 164rpx;
		height: 132rpx;
		margin: 20rpx 0 40rpx;
	}
	
	.que-hide-txt {
		margin-top: 20rpx;
		font-size: 22rpx;
		color: #C7C7C7;
	}
	
	/* 题目隐藏 */
	.que-hide-wrap {
		display: flex;	
		width: 50%;
		height: 300rpx;
		flex-direction: column;
		align-items: center;
	}
	
	.ans-hide-txt {
		display: inline-block;
		margin: 70rpx 0;
		font-size: 22rpx;
		color: #C7C7C7;
	}
	
	
	
</style>
